import withDefault from './default';
import _ from 'lodash';

export default (theme) => {
  return _.merge(withDefault(theme), {
    gridItem: {
      padding: theme.spacing(2),
      margin: '20px auto',
      textAlign: 'left',
      '&:first-child': {
        marginTop: '0px',
      },
    },
    logo: {
      height: '4em',
      marginBlockStart: '0.67em',
      marginBlockEnd: '0.67em',
    },
  });
};
