import React, { Component } from 'react';
import Fade from '@material-ui/core/Fade';
import ReactVisibilitySensor from 'react-visibility-sensor';
import { withStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';

const DEFAULT_TIMEOUT = {
  enter: 500,
  exit: 500,
};
const DEFAULT_STYLE = () => ({ image: { maxWidth: '100%' } });

class Image extends Component {
  static get propTypes() {
    return {
      timeout: PropTypes.number,
      classes: PropTypes.object,
    };
  }

  state = {
    checked: false,
  };

  componentWillUnmount() {
    this.setState({ checked: false });
  }

  handleChange = (event) => {
    if (!event) {
      this.setState({ checked: !event });
    } else {
      this.setState({ checked: true });
    }
  };

  render() {
    const { checked } = this.state;
    const { timeout, classes, ...props } = this.props;
    const actualTimeout = timeout || DEFAULT_TIMEOUT;

    return (
      <ReactVisibilitySensor intervalDelay={1000} onChange={this.handleChange}>
        <Fade in={checked} timeout={actualTimeout}><img className={classes.image} {...props} onLoad={this.handleChange} alt="" /></Fade>
      </ReactVisibilitySensor>
    );
  }
}

export default withStyles(DEFAULT_STYLE)(Image);
