import React, { useEffect, useState } from 'react';
import basicStyle from 'theming/styles/basic';
import theme from 'theming/themes/default';
import { withStyles, MuiThemeProvider } from '@material-ui/core/styles';
import withWidth from '@material-ui/core/withWidth';
import Grid from '@material-ui/core/Grid';
import { text } from 'theming/variables';
import PropTypes from 'prop-types';
import { Typography, Fade, FormControl, Button, RadioGroup, FormControlLabel, Radio, FormHelperText, TextField, CircularProgress } from '@material-ui/core';
import Logo from 'components/Logo';
import 'react-aspect-ratio/aspect-ratio.css';
import axios from 'axios';
import _ from 'lodash';
import queryString from 'query-string';
import { withSnackbar } from 'notistack';

const redColor = 'rgb(237, 41, 37)';

const styles = (stylingTheme) => {
  const defaultTheme = basicStyle(stylingTheme);
  return {
    ...defaultTheme,
    '@global': {
      ...defaultTheme['@global'],
      body: {
        ...defaultTheme['@global'].body,
        backgroundColor: redColor,
      },
      '#root': {
        backgroundColor: redColor,
      },
    },
    downloadWidget: {
      width: '250px',
      padding: '0',
    },
    mainHeader: {
      margin: '0px',
      textAlign: 'center',
      color: 'white',
      lineHeight: 1,
      paddingBottom: stylingTheme.spacing(2),
    },
    thanksHeader: {
      margin: '0px',
      textAlign: 'center',
      color: 'white',
      lineHeight: 1,
      paddingLeft: '10vw',
      paddingRight: '10vw',
      paddingBottom: stylingTheme.spacing(1),
    },
    thanksHeader2: {
      margin: '0px',
      textAlign: 'center',
      color: 'white',
      lineHeight: 1,
      paddingLeft: '10vw',
      paddingRight: '10vw',
      paddingBottom: stylingTheme.spacing(3),
    },
    unsubscribeSuccessful: {
      margin: '0px',
      textAlign: 'center',
      color: 'white',
      lineHeight: 1,
      paddingBottom: stylingTheme.spacing(3),
    },
    secondHeader: {
      fontWeight: 200,
      lineHeight: 1,
      fontSize: '1.2em',
      paddingBottom: '2vmax',
      textAlign: 'center',
      color: 'white',
    },
    secondHeaderOpt: {
      fontWeight: 200,
      lineHeight: 1,
      fontSize: '1em',
      textAlign: 'center',
      color: 'white',
    },
    thirdHeader: {
      fontWeight: 400,
      lineHeight: 1,
      fontSize: '1em',
      margin: '0px',
      textAlign: 'center',
      color: 'white',
    },
    enterButton: {
      maxWidth: 'calc(100% / 3 * 2)',
      width: 'calc(100% / 3 * 2)',
      display: 'inline-block',
      textTransform: 'initial',
      [stylingTheme.breakpoints.down('sm')]: {
        width: '100%',
        maxWidth: '100%',
      },
    },
    boldText: {
      fontFamily: text.generic,
      paddingTop: stylingTheme.typography.body1.fontSize,
      paddingBottom: stylingTheme.typography.body1.fontSize,
      textAlign: 'center',
    },
    mainBox: {
      padding: '6vmax 4vmax 0vmax 4vmax',
      textAlign: 'right',
      [stylingTheme.breakpoints.down('sm')]: {
        padding: '0vmax',
        textAlign: 'center',
      },
    },
    bottomBox: {
      padding: '0vmax 4vmax 0vmax 4vmax',
      textAlign: 'right',
    },
    introPage: {
      background: 'url(\'LandingPage3g_blank.png\') center center',
      backgroundSize: 'cover',
      backgroundRepeat: 'no-repeat',
      backgroundPosition: '50% 0%',
      backgroundAttachment: 'fixed',
      width: '100%',
      height: '60vh',
      padding: stylingTheme.spacing(3),
      [stylingTheme.breakpoints.down('xs')]: {
        height: '65vh',
      },
      [stylingTheme.breakpoints.down('sm')]: {
        height: '80vh',
      },
      [stylingTheme.breakpoints.down('md')]: {
        height: '75vh',
      },
      [stylingTheme.breakpoints.up('md')]: {
        height: '78vh',
      },
      [stylingTheme.breakpoints.up('lg')]: {
        height: '80vh',
      },
      [stylingTheme.breakpoints.up('xl')]: {
        height: '90vh',
      },
      backgroundColor: redColor,
    },
    logo: {
      ...defaultTheme.logo,
      position: 'fixed',
      padding: '2em',
      top: 0,
      left: 0,
    },
    footer: {
      backgroundColor: 'rgb(237, 41, 37)',
      position: 'fixed',
      height: '40vh',
      bottom: 0,
      padding: stylingTheme.spacing(3),
      paddingTop: stylingTheme.spacing(1),
      [stylingTheme.breakpoints.down('xs')]: {
        height: '30vh',
      },
      [stylingTheme.breakpoints.down('sm')]: {
        height: '35vh',
      },
      [stylingTheme.breakpoints.down('md')]: {
        height: '25vh',
      },
      [stylingTheme.breakpoints.up('md')]: {
        height: '22vh',
      },
      [stylingTheme.breakpoints.up('lg')]: {
        height: '20vh',
      },
      [stylingTheme.breakpoints.up('xl')]: {
        height: '10vh',
      },
    },
    footerText: {
      fontWeight: '200',
      paddingLeft: '10vw',
      paddingRight: '10vw',
      [stylingTheme.breakpoints.down('sm')]: {
        paddingLeft: '0',
        paddingRight: '0',
        textAlign: 'justify',
      },
      lineHeight: 1.5,
      fontSize: '1em',
      margin: '0px',
      textAlign: 'center',
      maxWidth: '800vw',
      color: 'white',
      '& a': {
        fontWeight: '600',
      },
    },
    root: {
      height: '200px',
    },
    edt: {
      margin: '0px',
      padding: '0px',
      color: 'black',
    },
    'MuiFilledInput-input': {
      padding: '0px',
    },
    '#outlined-basic': {
      padding: '0px',
    },
    form: {
      background: 'transparent',
    },
  };
};

const delay = ms => new Promise(
  resolve => setTimeout(resolve, ms),
);

function IntroPage({ classes, enqueueSnackbar, location }) {
  const [isUnsubscribeSuccess, setIsUnsubscribeSuccess] = useState(false);
  const [loading, setLoading] = useState(true);
  const [hasParams, setHasParams] = useState(false);
  const [reason, setSetReason] = React.useState('0');
  const [txtReason, setTextReason] = React.useState('');
  const [ids, setIds] = React.useState({});
  const [showThanks, setShowThanks] = React.useState(false);
  const [buttonEnabled, setButtonEnabled] = React.useState(false);
  const params = queryString.parse(location.search);

  const handleTextReasonChange = ((e) => {
    setTextReason(e.target.value);
  });

  const handleChange = ((e) => {
    setSetReason(e.target.value);
  });

  const onSubmit = async (event) => {
    event.preventDefault();
    setButtonEnabled(true);
    const response = await axios.post(process.env.REACT_APP_UNSUBSCRIBE_URL, {
      ...ids,
      reason,
      txtReason,
    },
      {
        headers: {
          'Access-Control-Allow-Origin': '*',
          'Access-Control-Allow-Credentials': true,
        },
      },
    );

    if (response.status === 200) {
      setShowThanks(true);
    } else {
      enqueueSnackbar('Unsubscription failed. Please try again later.', { variant: 'error' });
    }
  };

  const unsubscribeMember = async () => {
    try {
      const hasParameters = _.has(params, 'mk');
      setLoading(true);
      setHasParams(hasParameters);
      if (hasParameters) {
        const data = await axios(`${process.env.REACT_APP_UNSUBSCRIBE_URL}?mk=${params.mk}`);
        setIds({ member: data.data.member, log: data.data.log });
        await delay(1000);
        setIsUnsubscribeSuccess(true);
      } else {
        enqueueSnackbar('Unsubscription failed. Please try again later.', { variant: 'error' });
      }
    } catch (error) {
      enqueueSnackbar('Unsubscription failed. Please try again later.', { variant: 'error' });
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    unsubscribeMember();
  }, {});

  return (
    <MuiThemeProvider theme={theme}>
      <Grid
        container
        direction="row"
        justify="center"
        className={classes.introPage}
        alignItems="flex-end">
        <Grid
          item
          xs={12}
        >
          {!loading && isUnsubscribeSuccess && hasParams &&
            <Fade in direction="up" timeout={500}>
              <div>
                <Typography variant="h3" className={classes.mainHeader}>We Hate Goodbyes!</Typography>
                <Typography variant="h2" className={classes.secondHeader}>And really don&apos;t want to lose you - but we can take a hint</Typography>
                {!showThanks &&
                  <Typography variant="h1" className={classes.unsubscribeSuccessful}>Unsubscribe Successful</Typography>
                }
                {!showThanks &&
                  <Typography variant="h2" className={classes.secondHeaderOpt}>If you have a moment please let us know why you are leaving:</Typography>
                }
                {showThanks &&
                  <Grid container justify="center" alignItems="flex-start">
                    <Typography variant="h2" className={classes.unsubscribeSuccessful}>Thanks for the information.</Typography>
                  </Grid>
                }
                {!showThanks &&
                  <Grid container justify="center" alignItems="flex-start">
                    <form onSubmit={onSubmit}>
                      <FormControl component="fieldset" className={classes.form}>
                        <RadioGroup aria-label="reason" name="reason1">
                          <FormControlLabel value="0" control={<Radio />} onChange={handleChange} label="I no longer want to receive these emails" />
                          <FormControlLabel value="1" control={<Radio />} onChange={handleChange} label="I never signed up for this mailing list" />
                          <FormControlLabel value="2" control={<Radio />} onChange={handleChange} label="The emails are inappropriate" />
                          <FormControlLabel value="3" control={<Radio />} onChange={handleChange} label="The emails are spam" />
                          <FormControlLabel value="4" control={<Radio />} onChange={handleChange} label="Other (fill in reason below)" />
                        </RadioGroup>
                        {reason === '4' &&
                          <TextField
                            className={classes.edt}
                            onChange={handleTextReasonChange}
                            value={txtReason}
                            id="outlined-basic"
                            variant="filled"
                            size="small"
                            InputProps={{
                              disableUnderline: true,
                              margin: 'dense',
                              inputProps: {
                                style: {
                                  padding: '5%',
                                  paddingTop: '13px',
                                  paddingBottom: '13px',
                                },
                              },
                              style: {
                                color: 'black',
                                backgroundColor: 'transparent',
                                paddingLeft: '10px',
                              },
                            }} />
                        }
                        <FormHelperText />
                        <div>
                          {buttonEnabled &&
                            <CircularProgress color="white" style={{ display: 'block', margin: 'auto' }} />
                          }
                          {!buttonEnabled &&
                            <Button type="submit" variant="outlined" color="primary" disabled={buttonEnabled}>
                              Submit
                            </Button>
                          }

                        </div>
                      </FormControl>
                    </form>
                  </Grid>
                }
                {showThanks &&
                  <Typography variant="h4" className={classes.thirdHeader}>Even though you are unsubscribed, we can still be friends.</Typography>
                }
                {showThanks &&
                  <Typography variant="h4" className={classes.thirdHeader}>(less the benefits). And you are welcome back anytime</Typography>
                }
              </div>
            </Fade>
          }
          {!loading && !isUnsubscribeSuccess && hasParams &&
            <Fade in direction="up" timeout={500}>
              <div>
                <Typography variant="h1" className={classes.mainHeader}>OH DEAR!</Typography>
                <Typography variant="h2" className={classes.secondHeader}>You tried to say goodbye but there seems to have been some problems with processing the request.</Typography>
              </div>
            </Fade>
          }
        </Grid>
      </Grid>
      <Logo color="primary" href="https://www.bargainbooze.co.uk/club/" className={classes.logo} />
      <Grid container className={classes.footer} justify="center" alignItems="flex-start">
        {!loading && (
          <Grid item>
            <Typography variant="body1" color="secondary" className={classes.footerText}>
              {isUnsubscribeSuccess && (
                <span>
                  We have unsubscribed your email address&nbsp;
                </span>)}
              {!isUnsubscribeSuccess && (
                <span>
                  We suggest you try again but if you continue to face issues you can email us at:&nbsp;
                  <a href="mailto:gdpr@bestwayretail.co.uk">gdpr@bestwayretail.co.uk</a>&nbsp;and
                  we will unsubscribe your email address&nbsp;
                </span>)}
              from future communications from Bestway Retail Ltd
              (please note it can take up to 24 hours for this change to be processed)
              For more information about how we process personal information and use cookies,
              please read our&nbsp;
              <a href="https://www.bargainbooze.co.uk/wp-content/uploads/2020/02/privacypolicy.pdf">Privacy Policy&nbsp;</a>
              {isUnsubscribeSuccess && (
                <span>
                  or send us an email to&nbsp;
                  <a href="mailto:gdpr@bestwayretail.co.uk">gdpr@bestwayretail.co.uk</a>
                </span>)}
            </Typography>
          </Grid>
        )}
      </Grid>
    </MuiThemeProvider>
  );
}

IntroPage.propTypes = {
  classes: PropTypes.object,
  enqueueSnackbar: PropTypes.func,
  location: PropTypes.object,
  width: PropTypes.string,
};

export default
  withStyles(styles)(
    withWidth()(
      withSnackbar(IntroPage),
    ),
  );
