import _ from 'lodash';

class UserProfileService {
  fieldConsentMap = { firstName: 'firstNameConsent',
  gender: 'genderConsent',
  dob: 'dobConsent',
  postcode: 'postcodeConsent',
  mobile: 'mobileConsent' };

  setProfile(userProfile) {
    localStorage.setItem('userProfile', JSON.stringify(userProfile));
    return this;
  }

  parseProfile(userProfile) {
    const prevProfile = this.userProfile;
    Object.keys(userProfile).forEach((key) => {
      if (this.fieldConsentMap[key]) {
        if (prevProfile[key] !== userProfile[key]) {
          prevProfile.preferences[this.fieldConsentMap[key]] = true;
        }
        prevProfile[key] = userProfile[key];
      }
    });
    this.setProfile(prevProfile);
    return this;
  }

  parsePreferences(preferences) {
    const prevProfile = this.userProfile;
    Object.keys(preferences).forEach((key) => {
      prevProfile.preferences[key] = preferences[key];
    });
    this.setProfile(prevProfile);
    return this;
  }

  isExisting() {
    return this.userProfile && true;
  }

  get userProfile() {
    const userProfileString = localStorage.getItem('userProfile');
    try {
      return JSON.parse(userProfileString);
    } catch (e) {
      return {};
    }
  }

  userId() {
    return _.get(this.userProfile, 'userId');
  }
}

const userProfileService = new UserProfileService();
export default userProfileService;
