import 'react-perfect-scrollbar/dist/css/styles.css';
import 'typeface-poppins/index.css';
import UserProfileService from './services/userProfile';
import { BrowserRouter as Router, Route, Switch, Redirect } from 'react-router-dom';
import UnsubscribePage from './components/unsubscribe/UnsubscribePage';
import { SnackbarProvider } from 'notistack';

class App extends React.Component {
  state = {
    isLoggedIn: false,
  };

  componentWillMount() {
    this.setState({ isLoggedIn: UserProfileService.isExisting() });
  }

  handleLogin = (userProfile) => {
    UserProfileService.setProfile(userProfile);
    this.setState({ isLoggedIn: UserProfileService.isExisting() });
  }

  handleLogout = () => {
    UserProfileService.setProfile(null);
    this.setState({ isLoggedIn: UserProfileService.isExisting() });
  }

  renderWithRedirect(Component, props) {
    const { isLoggedIn } = this.state;
    return isLoggedIn ? (<Component {...props} />) : (<Redirect to="/login" />);
  }

  renderLoginWithRedirect(Component, props) {
    const { isLoggedIn } = this.state;
    return !isLoggedIn ? (<Component {...props} onLoginSuccess={this.handleLogin} />) : (<Redirect to="/main" />);
  }

  render() {
    return (
      <SnackbarProvider
      anchorOrigin={{
        vertical: 'top',
        horizontal: 'right',
      }}
      maxSnack={1}>
      <Router basename={`${process.env.REACT_APP_BASENAME}`}>
        <Switch>
          <Route exact path="/" component={UnsubscribePage} />
          {/* <Route exact path="/login" render={(props) => this.renderLoginWithRedirect(LoginPage, props)} />
          <Route exact path="/join" render={(props) => this.renderLoginWithRedirect(JoinPage, props)} />
          <Route exact path="/logout" component={(props) => <LogoutPage {...props} onLogoutSuccess={this.handleLogout} />} />
          <Route exact path="/password-forgotten" component={ForgotPasswordPage} />
          <Route exact path="/password/reset" render={(props) => <PasswordResetPage {...props} />} />
          <MainDrawer>
            <Route exact path="/main" render={(props) => this.renderWithRedirect(MainPage, props)} />
            <Route exact path="/faq" render={(props) => (<FAQPage {...props} />)} />
            <Route exact path="/my-club-id" render={(props) => this.renderWithRedirect(MyClubIdPage, props)} />
            <Route exact path="/terms-and-conditions" render={(props) => this.renderWithRedirect(TermsAndConditionsPage, props)} />
            <Route exact path="/my-club-id/preferences" render={(props) => this.renderWithRedirect(PreferencesPage, props)} />
          </MainDrawer> */}
        </Switch>
      </Router>
      </SnackbarProvider>
    );
  }
}

export default App;
