import { createMuiTheme } from '@material-ui/core/styles';
import createPalette from '@material-ui/core/styles/createPalette';
import createTypography from '@material-ui/core/styles/createTypography';
import { text, darkPalette, lightPalette } from 'theming/variables';

export const muiPalette = createPalette({
  type: 'dark',
  primary: darkPalette,
  secondary: lightPalette,
  background: {
    default: darkPalette.main,
  },
});

export const typography = createTypography(muiPalette, {
  fontFamily: text.generic,
  useNextVariants: true,
  lineHeight: 1.7,
  body1: {
    fontFamily: text.bold,
    lineHeight: text.lineHeight,
  },
  body2: {
    fontFamily: text.bold,
    fontSize: text.sizes.body2,
    lineHeight: text.lineHeight,
  },
  h6: {
    fontFamily: text.bold,
    lineHeight: text.lineHeight,
    fontWeight: '700',
  },
  inline: {
    fontFamily: text.generic,
  },
  h1: {
    fontSize: text.sizes.h1,
    fontFamily: text.bold,
    lineHeight: text.lineHeight,
    fontWeight: '700',
  },
  h2: {
    fontSize: text.sizes.h2,
    lineHeight: text.lineHeight,
    fontWeight: '700',
  },
  h3: {
    fontSize: text.sizes.h2,
    fontFamily: text.bold,
    lineHeight: text.lineHeight,
    fontWeight: '700',
  },
});

export default createMuiTheme({
  typography,
  spacing: 0,
  icon: 8,
  mixins: {
    body: {
      color: muiPalette.primary.contrastText,
      fontFamily: text.generic,
      lineHeight: text.lineHeight,
      width: '100%',
      height: '100vh',
      maxHeight: '100%',
      margin: 0,
    },
  },
  palette: muiPalette,
  overrides: {
    MuiIconButton: {
      root: {
        '&:hover': {
          backgroundColor: muiPalette.primary.main,
          boxShadow: '0px 0px 5px rgba(0, 0, 0, 0.6)',
        },
      },
    },
    MuiButton: {
      root: {
        borderRadius: '10vh',
        fontFamily: text.bold,
        fontWeight: '700',
        minWidth: '100%',
        border: '2px solid',
        backgroundColor: muiPalette.primary.light,
        color: muiPalette.primary.main,
        borderColor: muiPalette.primary.main,
        fontSize: text.sizes.button,
        padding: typography.body1.fontSize,
      },
      flatPrimary: {
        backgroundColor: muiPalette.primary.light,
        color: muiPalette.primary.main,
        borderColor: muiPalette.primary.main,
        fontSize: text.sizes.button,
        padding: typography.body1.fontSize,
      },
      textSecondary: {
        backgroundColor: muiPalette.primary.main,
        color: muiPalette.primary.light,
        borderColor: muiPalette.primary.light,
        fontSize: text.sizes.button,
        padding: typography.body1.fontSize,
        '&:hover': {
          backgroundColor: muiPalette.primary.light,
          color: muiPalette.primary.main,
          border: '2px solid',
          borderColor: muiPalette.primary.main,
        },
      },
      textPrimary: {
        '&:hover': {
          color: muiPalette.primary.light,
          fontSize: text.sizes.button,
          border: '2px solid',
          borderColor: muiPalette.secondary.main,
        },
      },
    },
    MuiFab: {
      label: {
        fontFamily: text.bold,
        fontWeight: '700',
      },
      root: {
        textTransform: 'none',
        boxShadow: 'none',
        height: '36px',
        width: '36px',
        minHeight: '36px',
        backgroundColor: muiPalette.primary.light,
        opacity: '0.6',
      },
    },
    MuiPaper: {
      root: {
        backgroundColor: muiPalette.primary.light,
        text: muiPalette.primary.text,
      },
      elevation4: {
        boxShadow: 'none',
      },
    },
    MuiFormControl: {
      root: {
        color: muiPalette.primary.contrastText,
        backgroundColor: muiPalette.primary.light,
        width: '100%',
        borderRadius: '2em',
        padding: '10px',
        margin: '10px',
      },
    },
    MuiFormLabel: {
      root: {
        color: muiPalette.primary.dark,
        fontFamily: text.bold,
        fontWeight: '700',
      },
    },
    MuiSpeedDial: {
      actions: {
        position: 'absolute',
        marginTop: '2px !important',
        width: '24px',
        '&$directionDown': {
          paddingTop: '24px',
        },
      },
      fab: {
        width: '24px',
        height: '24px',
        minHeight: 'inherit',
        backgroundColor: muiPalette.primary.dark,
        opacity: '0.8',
      },
    },
    MuiSpeedDialAction: {
      button: {
        width: '24px',
        height: '24px',
        minHeight: 'inherit',
        margin: '2px auto',
        backgroundColor: muiPalette.primary.dark,
        opacity: '0.8',
      },
    },
    MuiAppBar: {
      colorPrimary: {
        backgroundColor: muiPalette.primary.main,
        fontFamily: text.bold,
        fontWeight: '700',
      },
      positionFixed: {
        left: '20px',
      },
    },
    MuiTypography: {
      h3: {
        color: muiPalette.primary.black,
      },
      h1: {
        color: muiPalette.primary.light,
      },
    },
    MuiCardMedia: {
      root: {
        backgroundSize: 'contain',
        backgroundPosition: 'top',
      },
    },
    MuiCardContent: {
      root: {
        '&:last-child': {
          paddingBottom: 'default',
        },
      },
    },
  },
});
