import Image from './Image';
import React from 'react';
import { Link } from 'react-router-dom';
import _ from 'lodash';

const MAP = {
  secondary: 'dashboard_club_logo.svg',
  primary: 'club_logo_white.svg',
  root: 'club_logo_white.svg',
};

function Logo({ color, href, ...others }) {
  const imageURL = _.get(MAP, color, MAP.root);
  if (href) {
    return (
      <a href={href}>
        <Image src={`${imageURL}`} {...others} />
      </a>
    );
  }
  return (
    <Link to="/">
      <Image src={`${imageURL}`} {...others} />
    </Link>
  );
}

export default Logo;
